[web-theme = "dark"] {
  --productsButton-background-color: #804000;
  --productsButton-border-color: #804000;
  --productsButton-hover-background-color: #663300;
  --productsButton-hover-border-color: #cccc00;
  --productsButton-focus-box-shadow: rgba(128, 64, 0, 0.5);
  --productsBox-color: #dddddd;
  --productsName-color: #e6e6e6;
}

[web-theme = "light"] {
  --productsButton-background-color: #d9ffb3;
  --productsButton-border-color: #d9ffb3;
  --productsButton-hover-background-color: #b9db98;
  --productsButton-hover-border-color: #ff00ff;
  --productsButton-focus-box-shadow: rgba(185, 219, 152, 0.5);
  --productsBox-color: #000000;
  --productsName-color: #000000;
}


#productsButton {
  width: 95%;
  height: 95%;
  background-color: var(--productsButton-background-color);
  border-color: var(--productsButton-border-color);
}

#productsButton:hover {
  background-color: var(--productsButton-hover-background-color);
  border-color: var(--productsButton-hover-border-color);
}

#productsButton:focus {
  box-shadow: 0 0 0 0.25rem var(--productsButton-focus-box-shadow);
}

.productsBox {
  width: 16em;
  height: 20em;
  color: var(--productsBox-color);
}

.buttonInnerBox {
  margin-bottom: 50%;
}

.productsImg {
  width: 100%;
  height: 9em;
}

.productsName {
  text-align: left;
  font-size: 1.7em;
  margin-top: 0.3em;
  color: var(--productsName-color);
}

.productsPrice {
  text-align: right;
  font-size: 1.2em;
  margin-top: -2.0em;
  margin-right: 1.5em;
}
